import React from 'react'
import Dropdown from './Dropdown';
import { useState } from 'react';
import Datepicker from './Datepicker';
import { connect } from "react-redux";
import 'bootstrap/dist/css/bootstrap.min.css'
import {SET_USERID,SET_USERNAME,SET_USERMONTHOFFEES,SET_USERFEERECEIVED,SET_FEEAMOUNT} from '../Student/constant'


 function RegisterFee(props) {
    const [GetFeeAmount,SetFeeAmount] = useState(0);
    const studentoption = [{label:"Aarav Prajapati",value:1},{label:"Aayush Yadav",value:2},{label:"Alkama",value:3},{label:"Aradhya",value:4},{label:"Asraf",value:5},{label:"Drihi",value:6},{label:"Faizu",value:7},{label:"Hasnain",value:8},{label:"Haya",value:9},{label:"Kajal Yadav",value:10},{label:"Khushi",value:11},{label:"Kirti Shukla",value:12},{label:"Minal",value:13},{label:"Pihu Yadav",value:14},{label:"Rabiya",value:15},{label:"Shazida",value:16},{label:"Sohail",value:17},{label:"Sonakshi",value:18},{label:"Udit Dubey",value:19},{label:"Unnati Shukla",value:20},{label:"Vaibhav Dubey",value:21},{label:"Vihan Vishwakarma",value:22},{label:"Vinay",value:23}];
    const getText = (e)=> {
        props.SET_FEEAMOUNT(e.target.value);
        SetFeeAmount(e.target.value);
    }
    
    const  SaveFees=(e)=>{
        e.preventDefault();
        if(props.GET_REGISTERFEEARR.SET_USERID === 0 && props.GET_REGISTERFEEARR.SET_USERNAME === ''){
            alert('Please select student');
            return false;
        }
        else if(props.GET_REGISTERFEEARR.SET_USERMONTHOFFEES ===''){
            alert('Please select fee month');
            return false;
        }
        else if(props.GET_REGISTERFEEARR.SET_USERFEERECEIVED ===''){
            alert('Please select fee receive date');
            return false;
        }
        else if(props.GET_REGISTERFEEARR.SET_FEEAMOUNT === 0){
            alert('Fee Amount Could be 0');
            return false;
        }
    }
  
  return (
  <>
    <div className="container mt-5 mb-5 d-flex justify-content-left">
    <div className="card col-md-4">
        <div className="card-body">
            <h4 className="information mt-2">Register Fees</h4>
            <div className="row">
                <div className="col-sm-12">
                    <div className="form-group">
                            <Dropdown
                            id="ddstudents"
                            options={studentoption}
                            /> 
                    </div> 
                </div>
            </div>
            
            <div className="row">
                <div className="col-sm-12">
                    <div className="form-group">
                        <Datepicker showmonthyear={true} dateformat="MMM-yy" />
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-sm-12">
                    <div className="form-group">
                        <Datepicker showmonthyear={false} dateformat="dd-MMM-yy"/>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-sm-12">
                    <div className="form-group">
                        <div className="input-group">
                             <input className="form-control" value={GetFeeAmount} onChange={getText} type="number" placeholder="Fee Amount Rs."/> </div>
                    </div>
                </div>
            </div>
            <div className="d-flex flex-column text-center mt-3 mb-3">
            <button className="btn btn-primary btn-md-12 btn-block confirm-button" onClick={SaveFees} >Confirm</button>
         </div>
        </div>
    </div>
</div>
  </>
  )
}

function mapStateToProps(state) {
    console.log(state);
    return {
        GET_REGISTERFEEARR: state.RootReducer.RegisterFeeReducer,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        SET_USERID: data => dispatch({ type: SET_USERID, data: data }),
        SET_USERNAME : data =>  dispatch({type: SET_USERNAME ,data:data}),
        SET_USERMONTHOFFEES : data =>  dispatch({type: SET_USERMONTHOFFEES ,data:data}),
        SET_USERFEERECEIVED : data =>  dispatch({type: SET_USERFEERECEIVED ,data:data}),
        SET_FEEAMOUNT: data =>  dispatch({type: SET_FEEAMOUNT ,data:data})
    };
}


export default connect(
    mapStateToProps,
    mapDispatchToProps
)(RegisterFee);
 